"use strict";

var screens = [
        require("./screens/1"),
        require("./screens/2")
    ],
    state = {},

    css = require("./index.css");

m.mount(document.getElementById("mount"), {
    view : function () { return screens.map(function (screen, idx) { return m("div", {
                    class : css.screen
                },
                m("div", {
                    onclick : function () {
                        state.screen = idx;
                        console.log(state);
                    },
                    class : css.layer
                }),
                m(screen)
            ); }
        ); }
});



